import {Pipe, PipeTransform} from '@angular/core';
import {RxSelectedOptionsMap, RxSelectIndexedOption} from '../index';
import {isObject} from 'lodash-es';


@Pipe({
  name: 'isOptionSelected',
  pure: true
})
export class AdaptIsOptionSelectedPipe implements PipeTransform {
  transform(selectedOptionsMap: RxSelectedOptionsMap, indexedOption: RxSelectIndexedOption): boolean {
    return selectedOptionsMap.size > 0 && selectedOptionsMap.has(isObject(indexedOption.option) ? JSON.stringify(indexedOption.option).trim() : indexedOption.option);
  }
}
